import { getComments } from '@/api/article.js'
import { mapState } from 'vuex'
export default {
  name: 'ArticlesComments',
  props: {
    article: Object,
    reuqired: true
  },
  computed: {
    ...mapState(['user'])
  },
  data () {
    return {
      comments: []
    }
  },
  async mounted () {
    const { data } = await getComments(this.article.slug)
    this.comments = data.comments
  }
}
